body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  padding-top: 5%;
  font-size: 40px;
  font-weight: 500;
  color: white;
  background-color: RGB(182, 183, 242);
  height: 160px;
}

.App-Search {
  height: 42px;
  width: 50%;
  margin-left: 5%;
  margin-top: 2%;
  border-radius: 20px;
  padding-left: 20px;
  font-size: 20px;
  border: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.headContainer {
  margin-top: -7%;
  margin-left: -4%;
}

.search-icon {
  font-size: 25px;
  margin-left: -3%;
}

.songs {
  margin-left: 3%;
  margin-top: 4%;
  font-size: 20px;
  border: RGB(183, 183, 242) solid 2px;
  width: 40%;
  border-radius: 20px;
  padding-left: 18px;
  padding-top: 10px;
  padding-bottom: 10px;
}

.songContainer {
  cursor: pointer;
}

.Popup-Content {
  height: 90%;
  border-radius: 22px;
  padding-left: 3%;
  padding-top: 3%;
  padding-right: 3%;
}

.Loader{
  margin-left: 50%;
  padding-top: 8%;
}
